<template>
    <div>
        <h3></h3>
        <div class="box">
            <div class="item">
                    <p></p>
                    <div class="content">
                        <el-upload
                        class="upload-demo"
                        action="/api/upload/"
                        :on-success="handleSuccess"
                        :on-remove="handleRemove"
                        :file-list="fileList"
                        list-type="picture"
                        :before-upload='limitImage'
                        :limit='1'>
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/jpeg/png100kb</div>
                    </el-upload>
                    </div>
                
            </div>
        
            <div class="item">
                <p></p>
                <div class="content">
                    <el-select v-model="value" placeholder="">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                </div>
                
            </div>   

            <div class="item">
                <p></p>
                <div class="content">
                    <el-input type="text" v-model="title" placeholder=""></el-input>
                </div>
                
            </div>   

            <div class="item">
                <p>:</p>
                <div class="content">
                    <el-radio v-model="radio" label="0"></el-radio>
                <el-radio v-model="radio" label="1"></el-radio>
                </div>
            </div>  

            <div class="item">
                <p>:</p>
                <div class="content">
                    <el-input type="" v-model.number="count" placeholder=""></el-input>
                </div>
            </div>  
            
            <div class="item">
                <el-button type="success" class="pull-right" @click="submit"></el-button>
            </div>

        </div>
    </div>
    
</template>

<script>

import { work_select_id,work_edit,delete_pic } from 'api/works';
import { Message } from 'element-ui'

export default {
  async mounted(){
      //id
     this.work_id = this.$route.query.id*1;
     let res = await work_select_id({id:this.work_id});
        
    //
     if(res.pic_url)  this.fileList.splice(0,1,{url:res.pic_url});  //
     this.value = res.work_type;
     this.radio = res.home_show;
     this.count = res.view_count;
     this.title = res.work_title;
     this.url = res.pic_url;
     this.pic_name = res.pic_name;
   
  },
  data() {
    return {
      fileList: [], //
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      work_id:0, //id
      value: "", //
      radio: "", //
      count: "",
      url:"", //
      title:"",
      pic_name:""
    };
  },
  methods: {
    limitImage(file){
        let type = file.type;
        // 100k
        if(file.size/1024>100){
            Message.error({
                 message: ""
            })
             return false
        }
        if(type != ' image/jpeg ' &&  type != 'image/jpg' && type != 'image/png' ){
            Message.error({
                 message: ""
            })
            return false
        }
    },  
    //url
    handleSuccess(data) {
        this.url = data;
    },
    handleRemove() {
         //
        delete_pic({pic_name:this.pic_name,id:this.work_id});
        this.url = "";
        this.pic_name = "";
    },
    submit(){
       work_edit({
            id:this.work_id,
            type:this.value,
            url:this.url,
            title:this.title,
            show:this.radio,
            count:this.count,
            pic_name:this.pic_name
        });
    }
  }
};
</script>

<style lang="less" scoped>
.box {
  width: 500px;
  margin: 0 auto;
  .item {
    padding-left: 50px;
    margin-bottom: 20px;

    .content {
      padding-left: 60px;
    }
  }
}
</style>






